@font-face {
  font-family: 'olietta_script_poesia';
  src: url("../fonts/olietta_script_poesia.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

.allPosts a {
  font-weight: normal;
  color: #333;
  font-size: 14px;
}

.allPosts a:hover {
  color: #472047;
}

.allPosts div a {
  font-weight: 700;
  font-size: 16px;
  color: #507299;
  text-decoration: underline;
}

.allPosts span {
  font-weight: 700;
  color: black;
}

.allPosts ul {
  margin-left: 40px;
}